@import url(https://fonts.googleapis.com/css?family=Roboto:700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@-webkit-keyframes showTopText{0%{transform:translate3d(0, 100%, 0)}40%,60%{transform:translate3d(0, 50%, 0)}100%{transform:translate3d(0, 0, 0)}}@keyframes showTopText{0%{transform:translate3d(0, 100%, 0)}40%,60%{transform:translate3d(0, 50%, 0)}100%{transform:translate3d(0, 0, 0)}}@-webkit-keyframes showBottomText{0%{transform:translate3d(0, -100%, 0)}100%{transform:translate3d(0, 0, 0)}}@keyframes showBottomText{0%{transform:translate3d(0, -100%, 0)}100%{transform:translate3d(0, 0, 0)}}@-webkit-keyframes showArrows{0%{transform:translate3d(0, -100%, 0);opacity:0}100%{transform:translate3d(0, 0, 0);opacity:1}}@keyframes showArrows{0%{transform:translate3d(0, -100%, 0);opacity:0}100%{transform:translate3d(0, 0, 0);opacity:1}}html{scroll-behavior:smooth;background-color:#fafafa}#header{height:100vh}.animated-title,#link{color:#222;font-family:Roboto, Arial, sans-serif;height:90vmin;left:50%;position:absolute;top:50%;transform:translate(-50%, -50%);width:82vmin}.animated-title>div{height:50%;overflow:hidden;position:absolute;width:100%}.animated-title>div div{font-size:11vmin;padding:2vmin 0;position:absolute}.animated-title>div div span{display:block}.animated-title>div.text-top{border-bottom:1vmin solid #000;top:0}.animated-title>div.text-top div{-webkit-animation:showTopText 1s;animation:showTopText 1s;-webkit-animation-delay:0.5s;animation-delay:0.5s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards;bottom:0;transform:translate(0, 100%)}.animated-title>div.text-top div span:first-child{color:#767676}.animated-title>div.text-bottom,a{bottom:0}.animated-title>div.text-bottom .text{-webkit-animation:showBottomText 0.5s;animation:showBottomText 0.5s;-webkit-animation-delay:1.75s;animation-delay:1.75s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards;top:0;transform:translate(0, -100%)}.animated-title>div.text-bottom a{display:inline-block;-webkit-animation:showArrows 1s;animation:showArrows 1s;-webkit-animation-delay:2s;animation-delay:2s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards;transform:translate(0, -100%);top:200px;right:360px;font-size:60px}#header .text-bottom .arrows{width:0;font-size:3rem}#header .text-bottom .arrows:hover{color:#888888}.animated-title>div.text-bottom .arrows:hover{color:#2b012f}.sectionSecond{background:#2b012f;height:100vh;padding:5rem 24px;color:#fafafa;overflow:hidden}.sectionSecond:after{content:"";position:absolute;background:#452094;height:600px;width:100px;bottom:0;left:0;opacity:0}.sectionSecond .cover-img{width:40vw;height:100vh;float:right;right:6vw;bottom:18vh;position:relative}.sectionSecond .inner{position:relative;font-size:1.2rem;top:60px}.sectionSecond .inner h3{margin:8px 0;font-size:4rem;font-weight:900;word-wrap:break-word}.sectionSecond .inner p{width:50%;overflow-wrap:break-word}.sectionSecond .point{line-height:2;font-size:1.4rem}span{font-weight:600}.contact-btn{display:block;margin:4rem auto 0 auto;border-radius:4px;background-color:#2b012f;border:none;color:#fafafa;text-align:center;font-size:2rem;padding:20px;width:200px;transition:all 0.5s;cursor:pointer}.contact-btn span{cursor:pointer;display:inline-block;position:relative;transition:0.5s}.contact-btn span:after{content:'\00bb';position:absolute;opacity:0;top:0;right:-20px;transition:0.5s}.contact-btn:hover span{padding-right:25px}.contact-btn:hover span:after{opacity:1;right:0}.contact{text-align:center;margin:300px auto 400px auto;font-size:3rem;font-weight:400;background-color:#fafafa}*::-webkit-scrollbar{width:12px}*::-webkit-scrollbar-track{background:#dfe6e9}*::-webkit-scrollbar-thumb{background-color:#636e72;border-radius:20%;border:3px solid #dfe6e9}@media only screen and (max-width: 410px){.sectionSecond p .point{font-size:1.2rem;line-height:1.6}.sectionSecond h3{font-size:2.2rem !important}}@media only screen and (max-width: 1000px){.sectionSecond .inner p{width:100%}.sectionSecond .cover-img{width:0px}.sectionSecond .point{font-size:1.2em}}@media only screen and (max-height: 480px){.sectionSecond{padding-bottom:10rem !important;overflow:scroll}.sectionSecond .inner .cover-img{width:0}.sectionSecond .cover-img{width:0 !important}}

.contact-me{display:flex;flex-direction:column;background:#030101;background:linear-gradient(90deg, rgba(3,1,1,0.704919) 0%, #4a1f37 0%, #2b0120 19%, #2b012f 66%, #490437 99%);flex-wrap:wrap;align-items:center;justify-content:center;height:100vh;color:#fafafa;font-size:2rem}.contact-me .icons{font-size:5rem;margin-top:1rem;padding-bottom:300px}.contact-me .icons a{margin:2rem;cursor:pointer;color:#fafafa}.contact-me .icons a:hover,a:hover{color:#6132c5}.contact-me h2{font-size:1.8rem;margin:0}@media only screen and (max-width: 440px){.contact-me .icons a{margin:1.1rem;color:#fafafa}.contact-me h2{font-size:1.6rem}}@media only screen and (max-height: 450px){.contact-me .icons a{height:200vh;padding-bottom:0 !important}.contact-me .icons{padding-bottom:0 !important}}

